import React from 'react';
import styles from './ordered_product.module.scss'
import noData from './noData.svg'

function OrderedProduct(props) {
    return (
        <div className={styles.block}>
            <div className={`${styles.field}`}>
                {
                    props.product.picture
                        ? <img className={`${styles.img}`} src={props.product.picture} alt=""/>
                        : <img className={`${styles.img}`} src={noData} alt=""/>
                }

            </div>

            <div className={styles.field}>
                {
                    props.product.product_name
                        ? <div>:<span
                            className={styles.title}>{props.product.product_name.split(' ').slice(0, 5).join(' ')}</span></div>

                        : ' '
                }

            </div>

            <div className={styles.field}>
                {
                    props.product.product_count
                        ? <div>:<span className={styles.count}>{props.product.product_count}&nbsp;.</span>
                        </div>
                        : ' '
                }

            </div>

            <div className={styles.field}>
                {
                    props.product.price
                        ? <div>  1&nbsp;.:<span className={styles.price}>{props.product.price}&nbsp;.</span>
                        </div>
                        : ' '
                }

            </div>

            <div className={styles.field}>
                {
                    props.product.price && props.product.product_count
                        ? <div>
                             :
                            <span className={styles.total}>
                                {(props.product.price * props.product.product_count).toFixed(2)}&nbsp;.
                            </span>
                        </div>
                        : ' '
                }

            </div>
            <div className={styles.sep_line}> </div>
        </div>
    );
}

export default OrderedProduct;