import React from 'react';
import {connect} from "react-redux";
import styles from './products.module.scss'
import {NavLink} from "react-router-dom";

function Products(props) {
    let products = JSON.parse(localStorage.getItem('products_in_cart'))

    return (
        <div className={styles.products}>
            <div className={styles.title}>  :</div>
            <div className={styles.product}>
                <div className={`${styles.name} ${styles.name_cat}`}></div>
                <div className={`${styles.count} ${styles.name_cat}`}></div>
                <div className={`${styles.price} ${styles.name_cat}`}></div>
            </div>

            {products
                ? (products.length >= 1)
                    ? products.map(product => (
                        <div className={styles.product}>
                            <div className={styles.name}><NavLink
                                to={`/product/${product.id}`}>{product.name.split(' ').slice(0, 5).join(' ')}</NavLink>
                            </div>
                            <div className={styles.count}>{(product.count)}</div>
                            <div className={styles.price}>{parseFloat(product.price * (1 - (product.discount/100).toFixed(2))).toFixed(2)}&nbsp;.</div>
                        </div>
                    ))
                    : ''
                : ''
            }
            <div className={styles.total_price}>
                <div className={styles.total_price_title}> : <br/> <span
                    className={styles.price_text}>{parseFloat(props.products.total_cart).toFixed(2)}&nbsp;.</span></div>
                {props.auth.discount > 0
                    ? <div className={styles.total_price_discount}>   : <br/> <span
                        className={styles.price_text}>{((1 - (props.auth.discount / 100)) * parseFloat(props.products.total_cart)).toFixed(2)}&nbsp;.</span>
                    </div>
                    : ''
                }

            </div>
            <div className={styles.title}> :</div>
        </div>
    );
}

const mapStateToProps = state => {
    return {
        products: state.products,
        auth: state.auth,
    }
}

const mapDispatchToProps = {}

export default connect(mapStateToProps, mapDispatchToProps)(Products);